import wordList from './first-names.txt?raw';
let island = [];
let solutions = [];
let numIslanders = 0;
let TT_ROWS = 0;
const NUM_STATEMENTS = 10;

export function getIsland() {
    return island;
}

export function getSolutions() {
    return solutions;
}

export function KnightsAndKnaves(numIsl) {
    /* --------- Set up variables --------- */

    numIslanders = numIsl;
    TT_ROWS = (2 ** numIslanders);
    island = [];
    solutions = [];
    let puzzleValid = true;
    let names = wordList.split('\r\n');

    //Create island
    let namesCopy = names.slice();
    let repeatNames = false;
    for(let i = 0; i < numIslanders; ++i){
        //Continue even if names run out
        if(namesCopy.length == 0){
            namesCopy = names.slice();
            repeatNames = true;
        }

        //Randomly choose a name
        let nameInd = getRandomNumber(0, namesCopy.length - 1);
        let name = namesCopy[nameInd];
        if(!repeatNames){
            namesCopy.splice(nameInd, 1);
        }

        //Create islander
        let islander = {
            id: i,
            name: name,
            role: getRandomNumber(0, 1),
            valid: false,
            statementNum: 0,
            statement: "",
            others: []
        };
        island.push(islander);
    }

    for(let i = 0; i < numIslanders; ++i){
        setOthers(island[i], island);
    }

    do{
        /* --------- Generate puzzle --------- */

        //Randomly choose a statementNum and others
        for(let i = 0; i < numIslanders; ++i){
            if(!island[i].valid){
                island[i].statementNum = getRandomNumber(0, NUM_STATEMENTS - 1);
                setOthers(island[i], island);
            }
        }

        /* --------- Check validity --------- */

        //Set validity of all statements
        for(let i = 0; i < numIslanders; ++i){
            if(!island[i].valid){
                solve(island[i]);
            }
        }

        //Check whether all statements are valid
        puzzleValid = true;
        for(let i = 0; i < numIslanders; ++i){
            if(!island[i].valid){
                puzzleValid = false;
            }
        }

    }while(!puzzleValid);

    /* --------- Print prompt --------- */

    console.log("\n-------- Knights and Knaves --------\n");
    for(let i = 0; i < numIslanders; ++i){
        console.log(i+1 + "\t" + island[i].statement);
    }
        /*
        console.log("\nSolution " + 0 + ":");
        for(let i = 0; i < numIslanders; ++i){
            let role = island[i].role ? "knight" : "knave";
            console.log(i+1 + "\t" + island[i].name + " is a " + role);
        }
        */

    /* --------- Find all solutions --------- */

    //Create truth table values
    let TTvalues = [];
    for(let i = 0; i < numIslanders; ++i){
        let row = [];
        for(let j = 0; j < TT_ROWS; ++j){
            row.push(Math.floor(j / (2 ** i)) % 2);
        }
        TTvalues.push(row);
    }

    //Find which solutions are valid
    let solutionsRows = [];
    for(let j = 0; j < TT_ROWS; ++j){
        //Set roles based on truth table row
        let row = [];
        for(let i = 0; i < numIslanders; ++i){
            island[i].role = TTvalues[i][j];
            row.push(island[i].role);
        }

        //Set whether solution is valid
        for(let i = 0; i < numIslanders; ++i){
            solve(island[i]);
        }

        //Save the solution if it's valid
        let solutionValid = true;
        for(let i = 0; i < numIslanders; ++i){
            if(!island[i].valid){
                solutionValid = false;
                i = numIslanders;
            }
        }
        if(solutionValid){
            solutions.push(row);
            solutionsRows.push(j+1);
        }
    }

    /* --------- Print solutions --------- */

    console.log("\n-------- Solutions --------");

    let solNum = 1;
    for(let i = 0; i < solutions.length; ++i){
        console.log("\nSolution " + solNum++ + " (Attempt " + solutionsRows[i] + ")" + ":");
        for(let j = 0; j < numIslanders; ++j){
            let role = solutions[i][j] ? "knight" : "knave";
            console.log(j+1 + "\t" + island[j].name + " is a " + role);
        }
    }
    console.log("");
}

/* --------- Helper functions --------- */

function solve(speaker){
    let other = speaker.others[0];
    let other2 = speaker.others[1];
    
    //Set speaker's statement based on statementNum
    switch(speaker.statementNum){
        //Both knights
        case 0:
            if(speaker.role == (speaker.role && other.role)){
                speaker.statement = speaker.name + " says \"" + other.name + " and I are both knights\"";
                speaker.valid = true; 
                break;
            }
            speaker.valid = false;
            break;
        //Both knaves
        case 1:
            if(speaker.role == (!speaker.role && !other.role)){
                speaker.statement = speaker.name + " says \"" + other.name + " and I are both knaves\"";
                speaker.valid = true;
                break;
            }
            speaker.valid = false;
            break;
        //Both the same
        case 2:
            if(speaker.role == ((speaker.role == other.role))){
                speaker.statement = speaker.name + " says \"" + other.name + " and I are the same\"";
                speaker.valid = true;
                break;
            }
            speaker.valid = false;
            break;
        //Both different
        case 3:
            if(speaker.role == ((speaker.role != other.role))){
                speaker.statement = speaker.name + " says \"" + other.name + " and I are different\"";
                speaker.valid = true;
                break;
            }
            speaker.valid = false;
            break;
        //Other is knight
        case 4:
            if(speaker.role == other.role){
                speaker.statement = speaker.name + " says \"" + other.name + " is a knight\"";
                speaker.valid = true;
                break;
            }
            speaker.valid = false;
            break;
        //Other is knave
        case 5:
            if(speaker.role != other.role){
                speaker.statement = speaker.name + " says \"" + other.name + " is a knave\"";
                speaker.valid = true;
                break;
            }
            speaker.valid = false;
            break;
        //Three knights
        case 6:
            if(numIslanders >= 3){
                if(speaker.role == (speaker.role && other.role && other2.role)){
                    speaker.statement = speaker.name + " says \"" + other.name + ", " + other2.name + ", and I are all knights\"";
                    speaker.valid = true;
                    break;
                }
            }
            speaker.valid = false;
            break;
        //Three knaves
        case 7:
            if(numIslanders >= 3){
                if(speaker.role == (!speaker.role && !other.role && !other2.role)){
                    speaker.statement = speaker.name + " says \"" + other.name + ", " + other2.name + ", and I are all knaves\"";
                    speaker.valid = true;
                    break;
                }
            }
            speaker.valid = false;
            break;
        //Three same
        case 8:
            if(numIslanders >= 3){
                if(speaker.role == ((speaker.role == other.role) &&  (speaker.role == other2.role) && (other2.role == other.role))){
                    speaker.statement = speaker.name + " says \"" + other.name + ", " + other2.name + ", and I are all the same\"";
                    speaker.valid = true;
                    break;
                }
            }
            speaker.valid = false;
            break;
            //One of two others is a knight
            case 9:
                if(numIslanders >= 3){
                    if(speaker.role == ((other.role && !other2.role) || (!other.role && other2.role))){
                        speaker.statement = speaker.name + " says \"Either " + other.name + " or " + other2.name + " is a knight, but not both\"";
                        speaker.valid = true;
                        break;
                    }
                }
                speaker.valid = false;
                break;
        //Default
        default:
            speaker.valid = false;
    };
}

function setOthers(speaker, isle){
    //Set other and other2 to unique islanders from speaker and each other (if needed)
    let other = isle[getRandomNumber(0, numIslanders - 1)];
    while(other.name == speaker.name){
        other = isle[getRandomNumber(0, numIslanders - 1)];
    }
    let other2 = isle[getRandomNumber(0, numIslanders - 1)];
    if(numIslanders > 2){
        while((other2.name == speaker.name) || (other2.name == other.name)){
            other2 = isle[getRandomNumber(0, numIslanders - 1)];
        }
    }

    //Set others
    speaker.others = [other, other2];
}

// Gets a random number
function getRandomNumber(min, max){
    return Math.floor(Math.random() * (max - min + 1)) + min;
}