document.querySelector('#app').innerHTML = `
  <h1>Knights and Knaves</h1>
  <p id="rules">Islanders are either knights, who always tell the truth, or knaves, who always lie. Each islander makes a statement about themselves. Can you determine who is a knight and who is a knave?</p>
  <p>How many islanders would you like to generate?</p>
  <div id="controls">
    <input type="number" min="2" max="20" class="ui" id="numIslanders" placeholder="2-20" />
    <button class="ui" id="generateBtn">Create Puzzle</button>
  </div>
  <div class="content">
    <div id="promptContainer"></div>
    <div id="dropdownContainer"></div>
    <div id="solutionsContainer">
      <button class="ui" id="solutionsBtn">Show Solutions</button>
      <div id="solutionList"></div>
    </div>
  </div>
  `

document.querySelector('#generateBtn').addEventListener('click', function() {
  // Create and solve the puzzle
  const numIslanders = parseInt(document.getElementById('numIslanders').value, 10);
  if (numIslanders < 2 || numIslanders > 20 || isNaN(numIslanders)) {
    alert('Please enter a number between 2 and 20');
    return;
  }
  else {
    generatePuzzle(numIslanders);
  }
});

function generatePuzzle(numIslanders) {
  KnightsAndKnaves(numIslanders);
  let island = getIsland();
  
  // Set up prompts
  const promptContainer = document.getElementById('promptContainer');
  promptContainer.classList.add('primary-div');
  promptContainer.innerHTML = '';
  const promptTitle = document.createElement('p');
  promptTitle.textContent = 'Puzzle';
  promptTitle.classList.add('title');
  promptContainer.appendChild(promptTitle);
  for (let i = 0; i < numIslanders; i++) {
    const statement = document.createElement('p');
    statement.textContent = island[i].statement;
    promptContainer.appendChild(statement);
  }

  // Set up solutions
  const solutionsContainer = document.getElementById('solutionsContainer');
  solutionsContainer.classList.add('primary-div');
  const solutionsBtn = document.getElementById('solutionsBtn');
  solutionsBtn.style.display = 'block';
  const solutionList = document.getElementById('solutionList');
  solutionList.innerHTML = '';
  
  // Set up dropdowns
  const container = document.getElementById('dropdownContainer');
  container.style.backgroundColor = '#fafeff';
  container.classList.add('primary-div');
  container.innerHTML = '';
  const instructions = document.createElement('p');
  instructions.textContent = 'Can you solve the puzzle?';
  instructions.classList.add('title');
  container.appendChild(instructions);

  // Generate the specified number of dropdowns
  let nameID = 0;
  for (let i = 0; i < numIslanders/2; i++) {
    //Create a wrapper for each row
    const wrapper = document.createElement('div');
    wrapper.classList.add('dropdown-wrapper');

    //Create a row of 2 dropdowns
    for(let j = 0; j < 2; ++j) {
      if(nameID < numIslanders) {
        //Make islander name
        let islanderName = island[nameID++].name;
        const solution = document.createElement('p');
        solution.textContent = islanderName + ' is a...';
        
        //Create dropdown
        const select = document.createElement('select');
        select.classList.add('ui');
        select.id = 'answers'+(nameID-1);
        const option1 = document.createElement('option');
        option1.value = 'knight';
        option1.text = 'Knight';
        const option2 = document.createElement('option');
        option2.value = 'knave';
        option2.text = 'Knave';
        select.appendChild(option1);
        select.appendChild(option2);

        //Add each dropdown to the row
        wrapper.appendChild(solution);
        wrapper.appendChild(select);
      }
    }

    //Add each row to the container
    container.appendChild(wrapper);
  }
}

document.querySelector('#solutionsBtn').addEventListener('click', function() {
  // Setup
  let solutions = getSolutions();
  let island = getIsland();
  const solutionsBtn = document.getElementById('solutionsBtn');
  solutionsBtn.style.display = 'none';

  // Set up solutions and check player input
  const solutionsContainer = document.getElementById('solutionsContainer');
  let correct = true;
  for(let i = 0; i < solutions.length; i++) {
    correct = true;
    for(let j = 0; j < solutions[i].length; j++) {
      const type = solutions[i][j] === 0 ? 'knave' : 'knight';
      const selectedValue = document.getElementById('answers'+j).value;
      if(selectedValue !== type) {
        correct = false;
      }
    }
    if(correct) break;
  }

  // Display correct or incorrect message
  const dropdownContainer = document.getElementById('dropdownContainer');
  if(correct) {
    const correctMessage = document.createElement('p');
    correctMessage.textContent = 'Correct!';
    correctMessage.classList.add('title');
    dropdownContainer.style.backgroundColor = '#a1da98';
    dropdownContainer.appendChild(correctMessage);
  }
  else {
    const incorrectMessage = document.createElement('p');
    incorrectMessage.textContent = 'Incorrect';
    incorrectMessage.classList.add('title');
    dropdownContainer.style.backgroundColor = '#f7c1b5';
    dropdownContainer.appendChild(incorrectMessage);
  }
  
  // Title
  const solutionsTitle = document.createElement('p');
  solutionsTitle.textContent = 'Solutions';
  solutionsTitle.classList.add('title');

  // Solution list
  const solutionList = document.getElementById('solutionList');
  solutionList.innerHTML = '';
  solutionList.appendChild(solutionsTitle);
  for (let i = 0; i < solutions.length; i++) {
    const solution = document.createElement('p');
    solution.textContent = "Solution " + (i + 1) + ": ";
    solution.classList.add('solution');
    const solList = document.createElement('ul');
    for (let j = 0; j < solutions[i].length; j++) {
      const sol = document.createElement('li');
      const type = solutions[i][j] === 0 ? 'knave' : 'knight';
      sol.textContent = island[j].name + ' is a ' + type;
      solList.appendChild(sol);
    }
    solutionList.appendChild(solution);
    solutionList.appendChild(solList);
  }
  solutionsContainer.appendChild(solutionList);
});

  